#data-view {
  width: 100%;
  height: 100%;
  background-color: #030409;
  color: #fff;
}
#data-view #dv-full-screen-container {
  background-image: url(../../static/media/bg.837e99ea.png);
  background-size: 100% 100%;
  box-shadow: 0 0 3px blue;
  display: flex;
  flex-direction: column;
}
#data-view .main-content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
#data-view .block-left-right-content {
  flex: 1 1;
  display: flex;
  margin-top: 20px;
}
#data-view .block-top-bottom-content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-left: 20px;
}
#data-view .block-top-content {
  height: 55%;
  display: flex;
  flex-grow: 0;
  box-sizing: border-box;
  padding-bottom: 20px;
}
