#rose-chart {
  width: 30%;
  height: 100%;
  background-color: rgba(6, 30, 93, 0.5);
  border-top: 2px solid rgba(1, 153, 209, 0.5);
  box-sizing: border-box;
}
#rose-chart .rose-chart-title {
  height: 50px;
  font-weight: bold;
  text-indent: 20px;
  font-size: 20px;
  display: flex;
  align-items: center;
}
#rose-chart .dv-charts-container {
  height: calc(100% - 50px);
}
